	module.exports = {"data":{"site":{"siteMetadata":{"title":":-)","author":"github.com/9renpoto"}},"markdownRemark":{"id":"/home/travis/build/9renpoto/entry/src/pages/2017-05-07-prettier/index.md absPath of file >>> MarkdownRemark","html":"<h1>prettier</h1>\n<p><a href=\"https://github.com/prettier\">https://github.com/prettier</a></p>\n<p><a href=\"https://github.com/prettier/prettier\"><img src=\"https://img.shields.io/badge/styled_with-prettier-ff69b4.svg\" alt=\"styled with prettier\"></a></p>\n<p><code>eslint --fix</code> が最高だと思っていたがまた新たにformatterが出たので依存する。</p>\n<h2>eslint-config-prettier</h2>\n<p><a href=\"https://github.com/prettier/eslint-config-prettier\">https://github.com/prettier/eslint-config-prettier</a></p>\n<ul>\n<li>\n<p>設定</p>\n<ul>\n<li><a href=\"https://github.com/9renpoto/eslint-config/pull/25\">https://github.com/9renpoto/eslint-config/pull/25</a></li>\n</ul>\n</li>\n<li>\n<p>困りごと</p>\n<ul>\n<li><a href=\"https://github.com/standard/eslint-config-standard\">https://github.com/standard/eslint-config-standard</a> に依存している（<strong>セミコロン除外</strong>）と現状相性が悪い</li>\n<li>issueがあるようなので今後見直す</li>\n</ul>\n</li>\n</ul>\n<h2>prettier-eslint / prettier-eslint-cli</h2>\n<blockquote>\n<p>Code ➡️ prettier ➡️ eslint —fix ➡️ Formatted Code ✨</p>\n</blockquote>\n<p><a href=\"https://github.com/prettier/prettier-atom\">https://github.com/prettier/prettier-atom</a> に依存して様子を見る。\n現状若干重い。 <strong>上記セミコロン問題の取りこぼしなどがあり鶏卵になる。</strong></p>\n<h2>TypeScript</h2>\n<ul>\n<li><a href=\"https://github.com/eslint/typescript-eslint-parser\">https://github.com/eslint/typescript-eslint-parser</a> とは干渉して動かない</li>\n<li>TypeScriptのformatも計画されているようで期待</li>\n</ul>","frontmatter":{"title":"prettier 事始め","date":"May 07, 2017"}}},"pathContext":{"path":"/entry/2017/05/07/prettier/","_PARENT":"SOURCE"}}

